.author-item {
    display: flex;
    position: relative;
    padding: 5px 5px 5px 25px;
    border-radius: 3px;
    border: 1px solid #dadada;
    margin-top: 5px;
}

.author-item__delete {
    padding: 5px 4px;
    height: 20px;
    -ms-grid-row-align: center;
        align-self: center;
    font-size: 20px;
    border-radius: 50%;
    border: 1px solid rgba(117, 117, 117, 1);
    transition: all 333ms ease-in-out;
    color: rgba(117, 117, 117, 1);
    cursor: pointer;
    display: block;
    font-family: monospace;
    line-height: 10px;
    margin-left: auto;
    margin-right: 0;
}

.author-item__delete:hover {
    background-color: rgba(240, 0, 0, 1);
    border: 1px solid rgba(240, 0, 0, 1);
    color: white;
    box-shadow: 0 1px 2px -1px rgba(33, 33, 33, .3);
}

.author-item__move {
    position: absolute;
    left: 5px;
    line-height: 0;
    cursor: pointer;
    color: rgba(33, 33, 33, .4);
}

.author-item__move.author-item__up {
    top: 12px;
}

.author-item__move.author-item__down {
    bottom: 12px;
}

.author-item:first-child .author-item__up {
    display: none;
}

.author-item:last-child .author-item__down {
    display: none;
}

.author-item.is-new-author {
    border-color: forestgreen;
    border-width: 2px;
}

.author-item__nicename {
    display: block;
    font-size: 90%;
    color: rgba(99, 99, 99, .8);
}
